import React, { useState } from "react";
import "./tasklist.css";
import PropTypes from "prop-types";

/*
  export default function Tasklist(props)
  
  o props recebe varios parametros, sendo assim para pegar algum
  parametro dele, temos que escrever props.parametro, no entando
  com as chaves {} podemos informar que parametro em expecifico 
  queremos, como no exemplo abaixo


  OBS: as props não podem ser modificadas como uma
  variável por ex
*/
export default function Tasklist({ title }) {
  const [count, setCount] = useState(0);

  const increment = () => {
    setCount((currentCount) => {
      return currentCount + 1;
    });
  };

  return (
    <div className="tasklist">
      <div className="title">{title}</div>
      <div className="content">
        {count}
        <button onClick={increment}>Incrementar</button>
      </div>
    </div>
  );

  Tasklist.prototype = {
    title: PropTypes.string.isRequired
  };
}
